* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;

}

body {
  background: linear-gradient(
    90deg,
    rgb(39, 14, 204) 0%,
    rgb(120, 80, 194) 100%
  );
  min-height: 100vh;
}

.app {
  display: flex;
  flex-direction: column;
  width: 520px;
  min-height: 400px;
  background: #161a2b;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: rgb(137, 94, 255);
  font-size: 44px;
}

form {
  margin: 20px;
}

form button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  text-transform: capitalize;
}

form input::placeholder{
  color: rgb(137, 94, 255);

}

ul li {

  list-style: none;
  margin: 25px 0;
  display: flex;
  justify-content: space-between;
  padding: 15px;
  margin-left: 10%;
  align-items: center;
background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  width: 80%;
  border-radius: 5px;
  color: rgb(252, 207, 243);
  transition: background-color .2s ease-out;
}

ul li .row i {
  color: white;
margin-right: 70%;
  cursor: pointer;
  float: right;
  font-size: 25px;
  
}
ul li .row i:hover {
color :rgb(90, 7, 107)

}


footer{
  bottom: 0;
  position: fixed;
  width: 100%;
}
.footer {
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  height: 40px;
  margin: auto;
  width: 100%;
  text-align:center;
  padding:10px;
  color: rgb(185, 168, 235);
}

  
.to-do {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  margin-bottom: 32px;
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 320px;
  background: transparent;
  color: rgb(137, 94, 255);
}

@media only screen and (min-width: 600px) {
  /* For tablets: */
  .col-s-1 {width: 8.33%;}
  .col-s-2 {width: 16.66%;}
  .col-s-3 {width: 25%;}
  .col-s-4 {width: 33.33%;}
  .col-s-5 {width: 41.66%;}
  .col-s-6 {width: 50%;}
  .col-s-7 {width: 58.33%;}
  .col-s-8 {width: 66.66%;}
  .col-s-9 {width: 75%;}
  .col-s-10 {width: 83.33%;}
  .col-s-11 {width: 91.66%;}
  .col-s-12 {width: 100%;}
}
@media only screen and (min-width: 768px) {
  /* For desktop: */
  .col-1 {width: 8.33%;}
  .col-2 {width: 16.66%;}
  .col-3 {width: 25%;}
  .col-4 {width: 33.33%;}
  .col-5 {width: 41.66%;}
  .col-6 {width: 50%;}
  .col-7 {width: 58.33%;}
  .col-8 {width: 66.66%;}
  .col-9 {width: 75%;}
  .col-10 {width: 83.33%;}
  .col-11 {width: 91.66%;}
  .col-12 {width: 100%;}
}
